import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';

@Injectable({
    providedIn: 'root',
})
export class FacturacionService {
    constructor(private http: HttpClient) {}

    calcularPrecioUnitario(payload) {
        return this.http.post<any>(
            `${environment.ventas.vta_venta_calcular_unitario}`,
            payload
        );
    }

    getClientes(filter: string) {
        return this.http.get(environment.administracion.clientes + filter);
    }

    getDatosEmpresa() {
        return this.http.get(environment.ventas.get_datos_empresa);
    }

    getVentasAsociadas(filter: string) {
        return this.http.get(environment.ventas.view_vta_ventas + filter);
    }

    getArticulos(filter: string) {
        return this.http.get(environment.stock.view_articulos + filter);
    }

    getComprobantesAsociados(filter: string) {
        return this.http.get(environment.ventas.view_fe_facturas + filter);
    }

    getUltimoComprobanteAutorizado(payload) {
        return this.http.post(
            environment.ventas.ultimo_comprobante_autorizado,
            payload
        );
    }

    getFacturas(filter: string) {
        return this.http.get(environment.ventas.view_fe_facturas + filter);
    }

    getDatosFactura(filter: string) {
        return this.http.get(environment.ventas.get_datos_factura + filter);
    }

    createFactura(payload) {
        return this.http.post(environment.ventas.fe_facturas, payload);
    }

    editarFactura(payload, idFactura) {
        return this.http.put(
            environment.ventas.fe_facturas + '/' + idFactura,
            payload
        );
    }

    deleteFactura(codigoFactura: number) {
        return this.http.delete(
            environment.ventas.fe_facturas + '/' + codigoFactura
        );
    }

    generarComprobanteFactura(filter: string) {
        return this.http.get(
            environment.ventas.generar_comprobante_factura + filter
        );
    }

    imprimirFactura(filter) {
        return this.http.get(
            environment.ventas.reporte_comprobante_pdf + filter
        );
    }

    getDatosCobro(filter: string) {
        return this.http.get(environment.ventas.view_vta_cobros + filter);
    }

    asociarCobro(payload) {
        return this.http.post(environment.ventas.asociar_cobro, payload);
    }
}
