import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute, Router } from '@angular/router';
import { HeaderTextService } from '@shared/components/header-text/header-text.service';
import { EmpresaService } from '@shared/service/app/empresa.service';
import {
    debounceTime,
    distinctUntilChanged,
    startWith,
    Subject,
    takeUntil,
} from 'rxjs';
import { FacturacionService } from '../../services/facturacion.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MAT_DATE_FORMATS } from '@angular/material/core';
import { AppAlertService } from '@shared/service/app/alert.service';
import { MatDialog } from '@angular/material/dialog';
import { CalcularUnitarioComponent } from '../calcular-unitario/calcular-unitario.component';

const MY_DATE_FORMATS = {
    parse: {
        dateInput: 'mm/dd/yyyy',
    },
    display: {
        dateInput: 'dd/MM/yyyy',
        monthYearLabel: 'MMM yyyy',
        dateA11yLabel: 'LL',
        monthYearA11yLabel: 'MMMM yyyy',
    },
};

@Component({
    selector: 'app-guardar-factura',
    templateUrl: './guardar-factura.component.html',
    styleUrls: ['./guardar-factura.component.scss'],
    providers: [{ provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS }],
})
export class GuardarFacturaComponent implements OnInit, OnDestroy {
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    fechaActual: Date = new Date();

    spinner: boolean = false;

    totalIVA;
    bruto = 0;
    subtotal: number;
    faltaFacturarVista;
    montoFaltaFacturar;
    diferencia: number;

    observaciones: FormControl;

    clienteCobro;
    clientes;
    clientesSub;
    puntosVenta: [] = [];
    ultimoComprobanteAutorizado: number;
    comprobantes;
    comprobanteComboSub;
    puntoVentaSub;
    comprobantesAsociados: [] = [];
    ventasAsociadas: [] = [];
    facturaForm: FormGroup;

    articulosSub;
    articulos: [] = [];

    facturaDataSource = new MatTableDataSource<any>([]);

    cantidadSub;
    precioUnitarioSub;
    desdeCobranza: boolean;
    idFactura: number;
    factura;
    isEdicion: boolean = false;
    cobro = null;
    idCobro: number;
    idcliente: number;
    columns = [
        'detalle',
        'cantidad',
        'precio_unitario',
        'subtotal',
        'iva',
        'total',
        'acciones',
    ];
    disableGuardar: boolean;
    constructor(
        private router: Router,
        private route: ActivatedRoute,
        private textService: HeaderTextService,
        private empresaService: EmpresaService,
        private facturacionService: FacturacionService,
        private alert: AppAlertService,
        private dialog: MatDialog
    ) {
        this.textService.setTitulo(this.router);
        if (this.router.url.split('/').includes('guardar-factura')) {
            this.idCobro = Number(this.router.url.split('/')[3].split('?')[0]);
        } else {
            this.idCobro = null;
        }
        if (this.router.url.split('/').includes('editar-factura')) {
            this.idFactura = Number(
                this.router.url.split('/')[3].split('?')[0]
            );
            this.isEdicion = true;
        }
    }

    ngOnInit(): void {
        this.empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe((empresa) => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });

        if (this.isEdicion == true) {
            this.getDatosFactura().then((res) => {
                this.getData().then((res) => {
                    this.newForm().then((res) => {
                        this.calcularImportes();
                        this.spinner = false;
                    });
                });
            });
        } else {
            if (this.idCobro) {
                this.getDatosCobro().then((res) => {
                    this.getData().then((res) => {
                        this.newForm().then((res) => {
                            this.spinner = false;
                        });
                    });
                });
            } else {
                this.newForm().then((res) => {
                    this.spinner = false;
                });
                this.getData();
            }
        }
    }

    initObservadores() {
        this.cantidadSub = this.facturaForm
            .get('cantidad')
            .valueChanges.subscribe((value) => {
                if (value > 0) {
                    this.calcularImportes();
                } else {
                    this.alert.error('La cantidad no puede ser menor a 1');
                }
            });

        this.precioUnitarioSub = this.facturaForm
            .get('precioUnitario')
            .valueChanges.subscribe((value) => {
                if (value > 0) {
                    this.calcularImportes();
                } else {
                    this.alert.error(
                        'El precio unitario no puede ser menor a 1'
                    );
                }
            });

        this.articulosSub = this.facturaForm
            .get('articulo')
            .valueChanges.pipe(
                distinctUntilChanged(),
                debounceTime(350),
                startWith(' ')
            )
            .subscribe((value) => {
                if (typeof value == 'string') {
                    this.getArticulos(value);
                }
            });

        this.clientesSub = this.facturaForm
            .get('cliente')
            .valueChanges.pipe(distinctUntilChanged(), debounceTime(350))
            .subscribe((value) => {
                if (typeof value == 'string') {
                    this.getClientes(value);
                } else {
                    this.idcliente = value.codigo;
                    this.getVentasAsociadas();
                }
            });

        this.comprobanteComboSub = this.facturaForm
            .get('comprobante')
            .valueChanges.subscribe((value) => {
                if (value != String) {
                    this.getUltimoComprobanteAurorizado();
                }

                let isFactura = value.Desc.toLowerCase()
                    .split(' ')
                    .includes('factura');
                if (isFactura == true) {
                    this.facturaForm.get('comprobanteAsociado').setValue('');
                    this.facturaForm.get('comprobanteAsociado').disable();
                } else {
                    let isCredito = value.Desc.toLowerCase()
                        .normalize('NFD')
                        .replace(/[\u0300-\u036f]/g, '')
                        .split(' ')
                        .includes('credito');
                    let isDebito = value.Desc.toLowerCase()
                        .normalize('NFD')
                        .replace(/[\u0300-\u036f]/g, '')
                        .split(' ')
                        .includes('debito');
                    if (isCredito == true) {
                        let filter: string = `?filter[cliente]=${this.idcliente}&filter[generada]=1&filter[comprobante_asociado]=false`;
                        this.getComprobantesAsociados(filter);
                    }

                    if (isDebito == true) {
                        let filter: string = `?filter[cliente]=${this.idcliente}&filter[generada]=1&filter[comprobante_asociado]=true`;
                        this.getComprobantesAsociados(filter);
                    }
                    this.facturaForm.get('comprobanteAsociado').setValue('');
                    this.facturaForm.get('comprobanteAsociado').enable();
                }
            });

        this.puntoVentaSub = this.facturaForm
            .get('puntoVenta')
            .valueChanges.subscribe((value) => {
                if (value) {
                    this.getUltimoComprobanteAurorizado();
                }
            });
    }

    async getData(): Promise<void> {
        try {
            const resultados = await Promise.all([
                this.getClientes(),
                this.getDatosEmpresa(),
                this.getArticulos(' '),
            ]);
        } catch (error) {
            this.alert.error('Ocurrió un error al cargar los datos');
        }
    }

    getDatosFactura(): Promise<void> {
        this.spinner = true;
        return new Promise((resolve, reject) => {
            let filter = `?id=${this.idFactura}`;
            this.facturacionService.getDatosFactura(filter).subscribe({
                next: (res: any) => {
                    if (res.success == true) {
                        this.factura = res.result.factura;
                        this.facturaDataSource.data = res.result.items;
                        this.facturaDataSource.data.forEach((item) => {
                            item.total = item.cantidad * item.importe_unitario;
                        });
                        this.idcliente = this.factura.cliente;

                        resolve();
                    }
                },
            });
        });
    }

    newForm(): Promise<void> {
        return new Promise((resolve, reject) => {
            this.facturaForm = new FormGroup({
                fecha: new FormControl(
                    this.factura ? this.factura.fechaEmision : this.fechaActual,
                    Validators.required
                ),
                vencimientoPago: new FormControl(
                    this.factura
                        ? this.factura.fechaVencimientoPago
                        : this.fechaActual,
                    Validators.required
                ),
                fechaDesde: new FormControl(
                    this.factura
                        ? this.factura.fechaServicioDesde
                        : this.fechaActual,
                    Validators.required
                ),
                fechaHasta: new FormControl(
                    this.factura
                        ? this.factura.fechaServicioHasta
                        : this.fechaActual,
                    Validators.required
                ),
                cliente: new FormControl(''),
                comprobante: new FormControl(
                    this.factura ? this.factura.tipoComprobante : '',
                    Validators.required
                ),
                numero: new FormControl(
                    {
                        value: this.factura ? this.factura.numero : '',
                        disabled: true,
                    },
                    Validators.required
                ),
                puntoVenta: new FormControl(
                    this.factura ? Number(this.factura.puntoVenta) : '',
                    Validators.required
                ),
                tipoPago: new FormControl(
                    this.factura ? this.factura.contado : 0,
                    Validators.required
                ),
                comprobanteAsociado: new FormControl({
                    value: this.factura
                        ? this.factura.comprobante_asociado
                        : '',
                    disabled: this.factura?.comprobante_asociado ? false : true,
                }),
                ventaAsociada: new FormControl(
                    this.factura ? this.factura.idventa : '',
                    Validators.required
                ),
                articulo: new FormControl(''),
                cantidad: new FormControl(''),
                precioUnitario: new FormControl(''),
                total: new FormControl({
                    value: '',
                    disabled: true,
                }),
                detalle: new FormControl(''),
            });

            (this.observaciones = new FormControl(
                this.factura ? this.factura.observaciones : ''
            )),
                this.initObservadores();

            resolve();
        });
    }

    getUltimoComprobanteAurorizado() {
        let tipoComprobante = this.facturaForm.get('comprobante').value.Id;
        let puntoVenta = this.facturaForm.get('puntoVenta').value.toString();

        if (tipoComprobante && puntoVenta) {
            let payload = {
                puntoVenta: puntoVenta,
                tipoComprobante: tipoComprobante,
            };

            this.facturacionService
                .getUltimoComprobanteAutorizado(payload)
                .subscribe({
                    next: (res: any) => {
                        this.ultimoComprobanteAutorizado =
                            res.result.comprobanteNro;
                        this.facturaForm
                            .get('numero')
                            .setValue(this.ultimoComprobanteAutorizado);
                    },
                });
        }
    }

    calcularImportes() {
        let cantidad = this.facturaForm.get('cantidad').value;
        let precioUnitario = this.facturaForm.get('precioUnitario').value;
        let total = cantidad * precioUnitario;

        this.facturaForm.get('total').setValue(total);
        if (this.isEdicion == true) {
            this.facturaDataSource.data.forEach((item) => {
                item.subtotal = item.cantidad * item.importe_unitario;
            });
            this.subtotal = Number(
                this.facturaDataSource.data.reduce(
                    (acc, item) => acc + Number(item.subtotal),
                    0
                )
            );

            this.totalIVA = this.facturaDataSource.data.reduce(
                (acc, item) => acc + Number(item.subtotal_iva),
                0
            );

            this.bruto =
                this.facturaDataSource.data.reduce(
                    (acc, item) => acc + Number(item.total),
                    0
                ) + this.totalIVA;
        }
    }

    calcularPrecioUnitario() {
        const articuloSeleccionado = this.facturaForm.get('articulo').value;

        if (!articuloSeleccionado) {
            return this.alert.error('Seleccione un artículo válido');
        }

        this.dialog
            .open(CalcularUnitarioComponent, {
                data: articuloSeleccionado,
                panelClass: 'ayuda-dialog',
            })
            .afterClosed()
            .subscribe({
                next: (res) => {
                    if (res.success == true) {
                        this.facturaForm
                            .get('precioUnitario')
                            .setValue(res.result);
                    }
                },
            });
    }

    displayFn(cliente): string {
        return cliente && cliente.razonSocial ? cliente.razonSocial : '';
    }
    displayFnArticulos(articulo): string {
        return articulo && articulo.descripcion ? articulo.descripcion : '';
    }

    getArticulos(value?: string) {
        let filter: string =
            `?sort=descripcion&pagination=1&sort=-total_stock&per-page=10&page=0&filter[tipo][in][]=1&filter[tipo][in][]=2` +
            `&filter[descripcion][like]=${value}`;
        //let filterBusqueda: string = `&filter[descripcion][like]=${value}`;
        this.facturacionService.getArticulos(filter).subscribe({
            next: (res: any) => {
                this.articulos = res.result;
            },
        });
    }

    getClientes(value?) {
        let filter;
        if (
            (this.isEdicion == true && this.factura) ||
            (this.cobro && !value)
        ) {
            filter = `?filter[codigo]=${this.idcliente}`;
        } else {
            filter = value ? `?filter[razonSocial][like]=${value}` : '';
        }
        this.facturacionService.getClientes(filter).subscribe({
            next: (res: any) => {
                this.clientes = res.result;
                if (this.cobro) {
                    this.facturaForm.get('cliente').setValue(this.clientes[0]);
                    this.getVentasAsociadas();
                }
                if (this.factura && this.isEdicion == true) {
                    this.facturaForm.get('cliente').setValue(this.clientes[0]);
                }
            },
        });
    }

    getDatosEmpresa() {
        this.facturacionService.getDatosEmpresa().subscribe({
            next: (res: any) => {
                if (res.success == true) {
                    this.comprobantes = res.result.comprobantes;
                    let comprobantesValidos = ['factura', 'debito', 'credito'];
                    this.comprobantes = this.comprobantes.filter(
                        (comprobante: any) =>
                            comprobantesValidos.some((valid: string) =>
                                comprobante.Desc.toLowerCase()
                                    .normalize('NFD')
                                    .replace(/[\u0300-\u036f]/g, '')
                                    .split(' ')
                                    .includes(valid)
                            )
                    );

                    if (this.isEdicion == true && this.factura) {
                        let comprobanteSeleccionado = this.comprobantes.filter(
                            (comprobante) =>
                                comprobante.Id == this.factura.tipoComprobante
                        );

                        this.facturaForm
                            .get('comprobante')
                            .setValue(comprobanteSeleccionado[0]);
                    }
                    this.puntosVenta = res.result.puntosVenta;
                } else {
                    this.alert.error(res.mensaje);
                }
            },
        });
    }

    getVentasAsociadas() {
        let filter;
        filter = `?filter[idcliente]=${this.idcliente}`;
        this.facturacionService.getVentasAsociadas(filter).subscribe({
            next: (res: any) => {
                this.ventasAsociadas = res.result;
            },
        });
    }

    getComprobantesAsociados(value?) {
        let filterUltimoComprobante: string =
            value + `?filter[numero]=${this.ultimoComprobanteAutorizado}`;

        this.facturacionService
            .getComprobantesAsociados(filterUltimoComprobante)
            .subscribe({
                next: (res: any) => {
                    this.comprobantesAsociados = res.result;

                    if (
                        this.isEdicion == true &&
                        this.factura.comprobante_asociado
                    ) {
                        let comprobanteAsociado =
                            this.comprobantesAsociados.filter(
                                (comprobanteAsoc: any) =>
                                    comprobanteAsoc.codigo ==
                                    this.factura.comprobante_asociado
                            );

                        this.facturaForm
                            .get('comprobanteAsociado')
                            .setValue(comprobanteAsociado[0]);
                    }
                },
            });
    }

    getDatosCobro() {
        this.spinner = true;
        return new Promise((resolve, reject) => {
            let filter = `?filter[idcobro]=${this.idCobro}`;
            this.facturacionService.getDatosCobro(filter).subscribe({
                next: (res: any) => {
                    this.cobro = res.result[0];
                    this.idcliente = this.cobro.idcliente;
                    this.faltaFacturarVista = Number(this.cobro.a_facturar); //Para mostrar en la vista
                    this.montoFaltaFacturar = Number(this.cobro.a_facturar); //Para calculos
                    resolve(true);
                },
            });
        });
    }

    goBack() {
        this.router.navigate(['/facturas']);
    }

    formatDate(date: Date) {
        let fecha = new Date(date);
        let seconds = fecha.getSeconds().toString().padStart(2, '0');
        let minutes = fecha.getMinutes().toString().padStart(2, '0');
        let hours = fecha.getHours().toString().padStart(2, '0');
        let day = fecha.getDate().toString().padStart(2, '0');
        let month = (fecha.getMonth() + 1).toString().padStart(2, '0');
        let year = fecha.getFullYear();
        return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
    }

    async removeArticulo(articulo) {
        this.montoFaltaFacturar =
            (await this.montoFaltaFacturar) + articulo.total;
        this.subtotal = (await this.subtotal) - articulo.subtotal;
        this.totalIVA = (await this.totalIVA) - articulo.importeIVA;
        this.bruto = (await this.subtotal) + this.totalIVA;

        if (this.cobro) {
            this.faltaFacturarVista = this.montoFaltaFacturar;
            if (this.faltaFacturarVista == 0) {
                this.facturaForm.get('articulo').disable();
                this.facturaForm.get('cantidad').disable();
                this.facturaForm.get('precioUnitario').disable();
                this.facturaForm.get('detalle').disable();
            } else {
                this.facturaForm.get('articulo').enable();
                this.facturaForm.get('cantidad').enable();
                this.facturaForm.get('precioUnitario').enable();
                this.facturaForm.get('detalle').enable();
            }
        }

        this.facturaDataSource.data = this.facturaDataSource.data.filter(
            (item) => item.idarticulo != articulo.idarticulo
        );
    }

    addArticulo() {
        let articulo = this.facturaForm.get('articulo').value;
        let cantidad = this.facturaForm.get('cantidad').value;
        let precioUnitario = this.facturaForm.get('precioUnitario').value;
        let subtotal = cantidad * precioUnitario;
        let porcentajeIVA = Number(articulo.iva_porcentaje);
        let subtotal_iva = (porcentajeIVA / 100) * subtotal;
        let detalle = `${articulo.codigo_interno} - ${
            this.facturaForm.get('detalle').value || articulo.descripcion
        }`;

        // Se formatean los numeros a 2 decimales (como dice el backendista que hace el back)
        let totalArticulo = Number((subtotal + subtotal_iva).toFixed(2));

        let diferencia = Number((Math.abs(this.faltaFacturarVista - totalArticulo)).toFixed(2));

        if (this.cobro && diferencia > 10 && totalArticulo > this.faltaFacturarVista) {
            this.alert.error(
                'El monto total del artículo excede el monto total a facturar.'
            );
            return;
        }

        if (
            this.facturaDataSource.data.some(
                (itemTabla) => itemTabla.idarticulo == articulo.idarticulo
            )
        ) {
            this.alert.error('No puede agregar el mismo artículo');
            return;
        }
        if (
            !articulo ||
            !cantidad ||
            !precioUnitario ||
            !subtotal_iva ||
            !porcentajeIVA ||
            !subtotal ||
            !detalle ||
            !totalArticulo
        ) {
            this.alert.error('Revise los campos del artículo');
            return;
        }

        let itemTabla = {
            idarticulo: articulo.idarticulo,
            cantidad: cantidad,
            importe_unitario: precioUnitario,
            subtotal: subtotal,
            porcentajeIVA: porcentajeIVA,
            subtotal_iva: subtotal_iva,
            detalle: detalle,
            total: totalArticulo,
        };

        this.facturaDataSource.data = [
            ...this.facturaDataSource.data,
            itemTabla,
        ];

        this.subtotal = this.facturaDataSource.data.reduce(
            (acc, item) => acc + item.subtotal,
            0
        );

        this.totalIVA = this.facturaDataSource.data.reduce(
            (acc, item) => acc + item.subtotal_iva,
            0
        );

        this.bruto = this.facturaDataSource.data.reduce(
            (acc, item) => acc + item.total,
            0
        );

        this.facturaForm.get('total').setValue('');
        this.facturaForm.get('articulo').setValue('');
        this.facturaForm.get('cantidad').setValue('');
        this.facturaForm.get('precioUnitario').setValue('');
        this.facturaForm.get('detalle').setValue('');
        if (this.cobro) {
            this.montoFaltaFacturar = this.montoFaltaFacturar - totalArticulo;

            this.faltaFacturarVista = this.montoFaltaFacturar;
            if (this.faltaFacturarVista == 0) {
                this.facturaForm.get('articulo').disable();
                this.facturaForm.get('cantidad').disable();
                this.facturaForm.get('precioUnitario').disable();
                this.facturaForm.get('detalle').disable();
            } else {
                this.facturaForm.get('articulo').enable();
                this.facturaForm.get('cantidad').enable();
                this.facturaForm.get('precioUnitario').enable();
                this.facturaForm.get('detalle').enable();
            }
        }
    }

    onSubmit() {
        let fechaEmision = this.formatDate(this.facturaForm.get('fecha').value);
        let fechaVencimientoPago = this.formatDate(
            this.facturaForm.get('vencimientoPago').value
        );
        let fechaDesde = this.formatDate(
            this.facturaForm.get('fechaDesde').value
        );
        let fechaHasta = this.formatDate(
            this.facturaForm.get('fechaHasta').value
        );

        if (this.faltaFacturarVista > 10) {
            this.alert.error('La diferencia está fuera del rango permitido');
            return;
        }

        if (this.facturaForm.valid == false) {
            this.alert.error('Verifique los campos del formulario');
            return;
        }

        let payload = {
            factura: {
                fechaEmision: fechaEmision,
                fechaVencimientoPago: fechaVencimientoPago,
                cliente: this.facturaForm.get('cliente').value.codigo,
                tipoComprobante: this.facturaForm.get('comprobante').value.Id,
                puntoVenta: this.facturaForm.get('puntoVenta').value.toString(),
                fechaServicioDesde: fechaDesde,
                fechaServicioHasta: fechaHasta,
                contado: this.facturaForm.get('tipoPago').value,
                comprobante_asociado:
                    this.facturaForm.get('comprobanteAsociado').value.codigo ||
                    null,
                observaciones: this.observaciones.value || null,
                idventa: this.facturaForm.get('ventaAsociada').value || null, // opcional
                idcobro: this.idCobro || null,
                //venta: null, // opcional
                //idventapago: null, // opcional
                //idcobroformapago: null, // opcional
            },
            items: this.facturaDataSource.data,
        };

        payload.items.forEach((item) => {
            delete item.total;
            delete item.importe_bonificado;
            delete item.importe_total;
            delete item.factura;
            delete item.porcentaje_bonificado;
            delete item.porcentajeIVA;
            delete item.importeIVA;
            delete item.subtotal;
            delete item.idstkarticulo;
            delete item.subtotal_iva;
        });

        if (this.isEdicion == true) {
            payload.items.forEach((item) => {
                delete item.total;
                item.idstkarticulo != undefined
                    ? (item.idarticulo = item.idstkarticulo)
                    : '';
                delete item.importe_bonificado;
                delete item.importe_total;
                delete item.factura;
                delete item.porcentaje_bonificado;
                delete item.porcentajeIVA;
                delete item.importeIVA;
                delete item.subtotal;
                delete item.idstkarticulo;
                delete item.subtotal_iva;
            });

            this.facturacionService
                .editarFactura(payload, this.idFactura)
                .subscribe({
                    next: (res: any) => {
                        if ((res.success = true)) {
                            this.alert.success(res.mensaje);
                            this.router.navigate(['/facturas']);
                        } else {
                            this.alert.error(res.mensaje);
                        }
                    },
                });
        } else {
            this.facturacionService.createFactura(payload).subscribe({
                next: (res: any) => {
                    if ((res.success = true)) {
                        this.alert.success(res.mensaje);
                        this.router.navigate(['/facturas']);
                    } else {
                        this.alert.error(res.mensaje);
                    }
                },
            });
        }
    }

    ngOnDestroy(): void {}
}
